/* Container and layout */
.log-inventory-container {
    margin: 20px auto;
    max-width: 800px;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

/* Titles */
.page-title {
    font-size: 24px;
    text-align: center;
    margin-bottom: 20px;
    color: #333;
}

.section-title {
    font-size: 20px;
    margin-top: 30px;
    margin-bottom: 10px;
    color: #555;
}

/* Form styling */
.inventory-form {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    margin-bottom: 20px;
}

.form-group {
    flex: 1;
    display: flex;
    flex-direction: column;
}

.form-label {
    font-size: 14px;
    color: #333;
    margin-bottom: 5px;
}

.form-input {
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

/* Buttons */
.btn {
    padding: 10px 15px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
}

.primary-btn {
    background-color: #007bff;
    color: white;
}

.success-btn {
    background-color: #28a745;
    color: white;
}

.secondary-btn {
    background-color: #6c757d;
    color: white;
}

.btn:hover {
    opacity: 0.9;
}

/* Table styling */
.inventory-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
}

.inventory-table th,
.inventory-table td {
    padding: 12px;
    text-align: left;
    border-bottom: 1px solid black;
    color: black;
}

.inventory-table th {
    background-color: #007bff;
    color: white;
}

.empty-message {
    text-align: center;
    color: #999;
}