/* General Page Styling */
.login-page {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh; /* Ensures the background covers the full viewport height */
    background: linear-gradient(135deg, #3c8dbc, #3b5998);
    background-repeat: no-repeat;
    background-size: cover;
    font-family: 'Arial', sans-serif;
    margin: 0;
}

/* Card Container */
.login-card {
    width: 400px;
    padding: 20px 30px;
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    text-align: center;
}

/* Header Styling */
.login-header h1 {
    font-size: 24px;
    color: #333;
    margin-bottom: 10px;
}

.login-header p {
    font-size: 14px;
    color: #777;
}

/* Form Styling */
.login-form .form-group {
    margin-bottom: 20px;
    text-align: left;
}

.label {
    font-size: 14px;
    color: #333;
    margin-bottom: 8px;
    display: block;
}

.input-field {
    width: 100%;
    padding: 10px;
    font-size: 14px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.input-field:focus {
    border-color: #3c8dbc;
    box-shadow: 0 0 5px rgba(60, 141, 188, 0.5);
}

/* Error Message */
.error-message {
    color: #d9534f;
    font-size: 14px;
    margin: 10px 0;
}

/* Login Button */
.login-button {
    width: 100%;
    padding: 12px;
    background-color: #3c8dbc;
    color: #ffffff;
    font-size: 16px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.login-button:hover {
    background-color: #337ab7;
}

/* Links */
.login-links {
    margin-top: 20px;
}

.link {
    font-size: 14px;
    color: #3c8dbc;
    text-decoration: none;
    margin: 0 10px;
    transition: color 0.3s ease;
}

.link:hover {
    color: #23527c;
}
