.dashboard-home {
    text-align: center;
    padding: 20px;
}

.dashboard-home h2 {
    font-size: 24px;
    color: #4D148C;
    margin-bottom: 10px;
}

.dashboard-home p {
    font-size: 16px;
    color: #333;
    margin-bottom: 30px;
}

.cards-container {
    display: flex;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;
}

.dashboard-card {
    background-color: #FFFFFF;
    color: #4D148C;
    text-decoration: none;
    border: 1px solid #E4007C;
    border-radius: 10px;
    padding: 20px;
    width: 300px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.dashboard-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.dashboard-card h3 {
    font-size: 20px;
    margin-bottom: 10px;
}

.dashboard-card p {
    font-size: 14px;
    color: #333;
}
