/* General Page Styling */
.dashboard-page {
    display: flex;
    flex-direction: column;
    height: 100vh;
    background: linear-gradient(135deg, #4d148c, #e4007c); /* Metro Purple and Pink */
    font-family: 'Open Sans', sans-serif;
    color: #ffffff;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

/* Header */
.dashboard-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background-color: rgba(0, 0, 0, 0.7); /* Dark semi-transparent */
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

.dashboard-header h1 {
    font-size: 26px;
    margin: 0;
    font-weight: bold;
    color: #ffffff;
}

.logout-button {
    padding: 10px 20px;
    background-color: #ff4d4f; /* Bright red for logout */
    border: none;
    border-radius: 6px;
    color: #ffffff;
    cursor: pointer;
    font-size: 14px;
    font-weight: bold;
    transition: background-color 0.3s ease, transform 0.2s ease;
}

.logout-button:hover {
    background-color: #d9363e; /* Darker red on hover */
    transform: translateY(-2px); /* Lift effect */
}

.logout-button:active {
    transform: translateY(0);
}

/* Sidebar */
.dashboard-container {
    display: flex;
    height: calc(100% - 80px); /* Full height minus header */
}

.sidebar {
    width: 280px; /* Slightly wider for better readability */
    padding: 20px;
    background-color: rgba(0, 0, 0, 0.8); /* Dark sidebar */
    display: flex;
    flex-direction: column;
    align-items: start;
    box-shadow: 2px 0 8px rgba(0, 0, 0, 0.4); /* Bold shadow for separation */
}

.sidebar h2 {
    font-size: 22px;
    margin-bottom: 5px;
    font-weight: bold;
    color: #ffffff;
}

.sidebar p {
    font-size: 14px;
    margin-bottom: 20px;
    color: #d3d3d3; /* Light gray for subtle emphasis */
}

.sidebar-links {
    list-style: none;
    padding: 0;
    width: 100%;
}

.sidebar-link {
    display: block;
    padding: 12px 18px;
    margin: 8px 0;
    color: #ffffff;
    text-decoration: none;
    background-color: rgba(255, 255, 255, 0.1); /* Subtle background for links */
    border-radius: 6px;
    font-size: 16px;
    font-weight: 500;
    transition: background-color 0.3s ease, transform 0.2s ease;
}

.sidebar-link:hover {
    background-color: rgba(255, 255, 255, 0.3); /* Lighter hover effect */
    transform: translateX(5px); /* Slide effect */
}

/* Clock-In Button */
.clockin-button {
    margin-top: auto; /* Stick to the bottom */
    padding: 12px 20px;
    background-color: #28a745; /* Bright green for clock-in */
    color: #ffffff;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-size: 16px;
    font-weight: bold;
    transition: background-color 0.3s ease, transform 0.2s ease;
    width: 100%;
    text-align: center;
}

.clockin-button:hover {
    background-color: #218838; /* Darker green on hover */
    transform: translateY(-2px); /* Lift effect */
}

.clockin-button.disabled {
    background-color: #6c757d; /* Neutral gray for disabled */
    cursor: not-allowed;
}

/* Content Area */
.content {
    flex: 1;
    padding: 30px;
    background-color: #ffffff; /* Clean white content area */
    border-radius: 12px;
    margin: 20px;
    overflow-y: auto;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Subtle shadow for separation */
}

/* Headings and Paragraphs in Content */
.content h1,
.content h2,
.content h3 {
    color: #4d148c; /* Metro Purple for headings */
    font-weight: bold;
}

.content p {
    font-size: 14px;
    color: #333333; /* Dark gray text */
}

.error-banner {
    color: red;
    background-color: #fdd;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid red;
    border-radius: 5px;
}
