/* General Page Styling */
.todos-page {
    padding: 20px;
    font-family: 'Inter', sans-serif;
    /* Modern sans-serif font */
    color: #333;
    /* Neutral text color */
    background: linear-gradient(135deg, #ececec, #f7f7f7);
    /* Subtle gradient */
    min-height: 100vh;
}

/* Header */
.todos-page h1 {
    font-size: 2rem;
    font-weight: 600;
    text-align: center;
    color: #4d148c;
    /* Metro Purple */
    margin-bottom: 20px;
}

/* Todos List Styling */
.todos-list {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background: #ffffff;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    /* Subtle shadow for depth */
    border-radius: 12px;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.todos-list:hover {
    transform: translateY(-5px);
    /* Subtle lift effect */
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
}

.todos-list h2 {
    font-size: 1.5rem;
    margin-bottom: 10px;
    color: #4d148c;
    text-transform: uppercase;
    font-weight: bold;
    border-bottom: 2px solid #ececec;
    /* Section separator */
    padding-bottom: 5px;
}

/* Empty State */
.todos-list p {
    text-align: center;
    font-size: 1rem;
    font-style: italic;
    color: #888;
    /* Neutral color for empty state */
    padding: 10px 0;
}

/* Todo Item Styling */
.todo-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 16px;
    margin: 10px 0;
    border-radius: 8px;
    transition: background-color 0.3s ease, transform 0.2s ease;
    cursor: pointer;
    font-size: 1rem;
}

/* Pending Todos */
.todo-item.pending {
    background-color: #e6ffe6;
    /* Light green for pending todos */
    border: 1px solid #b3ffb3;
    /* Subtle border for pending todos */
    font-weight: 500;
}

.todo-item.pending:hover {
    background-color: #d1ffd1;
    /* Darker green on hover */
    transform: scale(1.02);
    /* Slight zoom effect */
}

/* Completed Todos */
.todo-item.completed {
    background-color: #fff5f5;
    /* Light red for completed todos */
    border: 1px solid #ffc6c6;
    /* Subtle border for completed todos */
    color: #888;
    /* Muted text for completed todos */
    text-decoration: line-through;
    font-weight: 400;
    transition: background-color 0.3s ease, transform 0.2s ease;
}

.todo-item.completed:hover {
    background-color: #ffe5e5;
    /* Slightly darker red on hover */
    transform: scale(1.02);
    /* Slight zoom effect */
}

/* Checkbox Styling */
.todo-item input[type="checkbox"] {
    width: 20px;
    height: 20px;
    margin-right: 10px;
    accent-color: #4d148c;
    /* Purple accent for modern look */
    cursor: pointer;
    transform: scale(1.1);
    transition: transform 0.2s ease;
}

.todo-item input[type="checkbox"]:hover {
    transform: scale(1.2);
    /* Slight zoom on hover */
}

/* Smooth Animations */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(10px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.todo-item {
    animation: fadeIn 0.4s ease;
}

/* Responsive Design */
@media (max-width: 768px) {
    .todos-page {
        padding: 10px;
    }

    .todos-list {
        max-width: 100%;
        padding: 15px;
    }

    .todo-item {
        font-size: 0.9rem;
    }

    .todos-page h1 {
        font-size: 1.8rem;
    }
}