/* Import Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap');

body {
    font-family: 'Open Sans', sans-serif;
    background-color: #F5F5F5; /* Neutral light background */
    color: #333333; /* Neutral dark gray text */
}

.eod-report-container {
    max-width: 1000px;
    margin: 20px auto;
    padding: 20px;
    background-color: #FFFFFF; /* White card */
    border-radius: 10px; /* Rounded corners */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Subtle shadow */
}

.eod-report-title {
    text-align: center;
    font-size: 28px; /* Slightly larger */
    font-weight: 600;
    margin-bottom: 20px;
    color: #4D148C; /* Metro Purple */
}

.info-grid {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    padding: 15px;
    background-color: #EFEFEF; /* Light gray background */
    border: 2px solid #4D148C; /* Metro Purple border */
    border-radius: 8px;
    color: #333333; /* Neutral text */
    font-weight: 600;
    font-size: 16px; /* Slightly larger font */
}

.info-grid div {
    flex: 1;
    text-align: center; /* Center-align text */
    color: #4D148C; /* Metro Purple text */
}


.form-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 20px;
}

.form-group {
    display: flex;
    flex-direction: column;
}

label {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 5px;
    color: #4D148C; /* Metro Purple */
}

.form-input {
    padding: 10px;
    font-size: 14px;
    border: 1px solid #CCCCCC; /* Subtle border */
    border-radius: 8px;
    transition: all 0.3s ease;
}

.form-input:focus {
    outline: none;
    border-color: #E4007C; /* Metro Pink */
    box-shadow: 0 0 6px rgba(228, 0, 124, 0.5); /* Pink glow */
}

.form-output {
    font-size: 16px;
    font-weight: 600;
    color: #E4007C; /* Metro Pink for emphasis */
}

.form-submit-button {
    margin-top: 20px;
    padding: 15px;
    background-color: #4D148C; /* Metro Purple */
    color: #FFFFFF;
    border: none;
    font-size: 16px;
    font-weight: 600;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
    width: 100%;
}

.form-submit-button:hover {
    background-color: #3B0F6B; /* Darker Purple */
    transform: translateY(-2px); /* Subtle lift */
}

.form-submit-button:active {
    background-color: #4D148C;
    transform: translateY(0); /* Reset lift */
}
